
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import Pair from 'common/Pair.vue'
import HistoryOrder from 'models/HistoryOrder'
import { HistoryOrder as HistoryOrderInterface } from '@/modules/store/data/resources'

@Component({
  name: 'HistoryOrderDetailsCancelled',
  components: {
    Pair
  }
})
export default class HistoryOrderDetailsCancelled extends Vue {
  @Prop() details!: HistoryOrderInterface

  get partnerShouldPay(): boolean {
    return !!HistoryOrder.shouldPay(this.details)
  }

  get additionalInfo(): string {
    if (this.details.acceptanceTime && !this.partnerShouldPay)
      return `${this.$t('order.cancellation.accepted.by.partner')}`
    if (!this.details.acceptanceTime)
      return `${this.$t('order.cancellation.before.accepted')}`
    return ''
  }

  get cancelReason(): string {
    return `${this.$t(`cancelReason.${this.details.cancelReason}`)}. ${
      this.additionalInfo
    }`
  }

  get amountToPayToPartner(): string | number {
    return this.partnerShouldPay ? 0 : this.details.products!.totalProductsPrice
  }
}
